import React from 'react';
import { NavLink } from 'react-router-dom';

function Navbar() {
  return (
    <nav className="navbar">
      <NavLink exact to="/about" className="item" activeClassName="on">
        About
      </NavLink>
      <br />
      <NavLink exact to="/" className="item" activeClassName="on">
        Home
      </NavLink>
    </nav>
  );
}

export default Navbar;
