import { useEffect } from 'react';
import { useHistory } from 'react-router-dom';

function home() {
  const isLoggedin = false;
  const lol = useHistory();
  useEffect(() => {
    if (isLoggedin) {
      console.log('/');
    } else {
      lol.push('/dyno/nello');
    }
  }, [isLoggedin, lol]);
  return (
    <div className="div">
      <h1>It is home page</h1>
      <p>
        Walla It is home page. Lorem ipsum dolor sit amet consectetur adipisicing elit. Consequatur
        magnam aperiam vero consectetur esse atque laudantium laborum deleniti optio fugit?{' '}
      </p>
    </div>
  );
}

export default home;
